@import '../../../../assets/css/mixins';

.btn {
  margin-left: auto;
  height: 40px;
  background: #8c51ff;
  border-radius: 18px;
  padding-right: 16px;
  text-align: right;
  line-height: 40px;
  font-weight: 600;
  font-size: 16px;
  color: #ffffff !important;
  position: relative;
  display: block;
  padding-left: 64px;
  @include lightThemeOnly {
    background: rgba(114, 47, 255, 1);
  }

  @include mobile-only {
    height: 29px;
    line-height: 29px;
    font-size: 12px;
    margin-top: 22px;
    padding-right: 12px;
    margin-left: 16px;
    padding-left: 36px;
    width: fit-content;
  }

  &:hover {
    color: #ffffff;
  }
  &_icon {
    position: absolute;
    width: 57px;
    left: 0;
    bottom: 0;

    @include mobile-only {
      width: 45px;
      left: -16px;
    }
  }
}
