@import '../../../assets/css/theme.scss';
@import '../../../assets/css/var.scss';
@import '../../../assets/css/mixins';

.sun_locking_box {
  background: $color-info-card-bg;
  border-radius: 10px;
  padding: 18px 20px;
  @include lightThemeOnly {
    border: solid 1px $color-border-secondary;
  }
  &:nth-child(2) {
    margin: 0 0 0 10px;
  }
}

.farm_flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.sun_logo {
  width: 56px;
  height: 56px;
}

.sun_locking_title {
  font-size: 20px;
  font-weight: 600;
  color: $color-text-default;
}

.sun_locking_subtitle {
  font-size: 14px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: $color-text-default-5;
}

.claim_label {
  font-size: 14px;
  font-weight: 600;
  color: $color-text-default;
  opacity: 0.5;
}

.claim_text {
  font-size: 16px;
  font-weight: 600;
  line-height: 22px;
  color: $color-text-default;
}

.claim_link {
  font-size: 14px;
  font-weight: 600;
  color: $color-a;
  cursor: pointer;
  img {
    transform: rotate(90deg);
    margin-left: 4px;
    vertical-align: baseline;
  }
}

.w_60 {
  width: 60%;
}

.locking_margin {
  margin-left: 16px;
}

@include mobile-only {
  .farm_flex {
    display: block;
    > button {
      width: 100%;
      margin-top: 16px;
    }
    .locking_margin {
      margin-left: 0px;
      margin-top: 16px;
    }
  }

  .sun_locking_box {
    padding: 10px;
    &:nth-child(2) {
      margin: 10px 0 0 0;
    }
  }
  .claim_label {
    font-size: 12px;
    line-height: 20px;
  }
  .claim_link {
    font-size: 12px;
  }
}
