@import '../../../assets/css/theme.scss';
@import '../../../assets/css/var.scss';

.liquidity_card {
  background: $color-large-range-card-bg;
  border-radius: 10px;
  padding: 12px;
  margin-bottom: 16px;
  &:last-child {
    margin-bottom: 0;
  }
}

.liq_logo {
  display: flex;
  align-items: center;
  img {
    height: 20px;
  }
  > span:nth-child(2) {
    font-size: 16px;
    font-weight: 600;
    color: $color-text-default;
    margin-left: 8px;
  }
  > span:nth-child(3) {
    font-size: 12px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: $color-text-default;
    opacity: 0.5;
    margin-left: 12px;
  }
}

.stake_flex {
  display: flex;
  align-items: stretch;
}

.stake_margin {
  > div:nth-child(1) {
    margin: 0 10px 0 0;
  }
}

@include mobile-only {
  .stake_flex {
    flex-direction: column;
  }
  .stake_margin {
    margin-top: 20px;
    > div:nth-child(1) {
      margin: 0 0 10px 0;
    }
  }
  .liquidity_card {
    padding: 10px;
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0;
    }
  }
}
