@import '../../../assets/css/theme.scss';
@import '../../../assets/css/var.scss';

.stake_title {
  display: flex;
  align-items: center;
  img {
    height: 30px;
  }
  > span:nth-child(2) {
    font-size: 20px;
    color: $color-text-default;
    margin-left: 6px;
  }
  > span:nth-child(3) {
    font-size: 12px;
    font-weight: 600;
    color: $color-text-default-5;
    margin: 0 6px 0 12px;
  }
  > span:nth-child(4) {
    font-size: 14px;
    font-weight: 600;
    color: $color-text-default;
  }
}

.stake_info {
  margin-top: 20px;
  .stake_info_dep {
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;
    > span:nth-child(1) {
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: $color-text-default-7;
    }
    > span:nth-child(2) {
      font-size: 16px;
      font-weight: 600;
      color: $color-text-default;
    }
  }
  > div {
    display: flex;
    justify-content: space-between;
    margin-bottom: 12px;
    > span:nth-child(1) {
      font-size: 12px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: $color-text-default-5;
    }
    > span:nth-child(2) {
      font-size: 14px;
      font-weight: 600;
      color: $color-text-default;
    }
  }
  .stake_link {
    font-size: 12px;
    margin-left: 10px;
  }
}

.stake_content {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: column;
  min-height: 90px;
  &_left {
    > div:nth-child(1) {
      font-size: 16px;
      font-weight: 600;
      color: $color-text-default;
      margin-bottom: 6px;
      line-height: 20px;
    }
    > div:nth-child(2) {
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: $color-text-default-5;
      white-space: nowrap;
      line-height: 17px;
    }
  }

  &_right {
    > span:nth-child(1) {
      font-size: 14px;
      font-weight: 600;
      color: $color-text-default-5;
      margin-right: 8px;
    }
    > span:nth-child(2) {
      font-size: 16px;
      font-weight: 600;
      color: $color-text-default;
    }
  }

  &_btn {
    width: 100%;
    margin-top: 16px;
  }
}

.stake_flex {
  display: flex;
  align-items: stretch;
}

.stake_margin {
  margin-top: 10px;
  > div:nth-child(1) {
    margin: 0 10px 0 0;
  }
}

.stake_box {
  width: 100%;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-between;
}

.w_60 {
  width: 60%;
}

.w_40 {
  width: 40%;
}

@include mobile-only {
  .stake_content {
    flex-direction: initial;
    min-height: initial;
    &_right {
      margin-top: 0;
      white-space: nowrap;
    }
    &_right {
      > span:nth-child(1) {
        font-size: 12px;
      }
      > span:nth-child(2) {
        font-size: 14px;
      }
    }
  }
  .stake_flex {
    flex-direction: column;
  }
  .stake_margin {
    > div:nth-child(1) {
      margin: 0 0 10px 0;
    }
  }
}
