@import '../../../../assets/css/theme';
@import '../../../../assets/css/var.scss';

.pool_flex {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  img {
    height: 40px;
  }
}

.pool_title {
  font-size: 16px;
  font-weight: 600;
  color: $color-text-default;
}

.pool_sub_title {
  font-size: 12px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: $color-text-default-5;
  margin-top: 6px;
}

.pool_help {
  background: $color-button-primary;
  border-radius: 15px;
  padding: 6px 16px 6px 40px;
  position: relative;
  font-size: 12px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: $white;
  display: inherit;
  cursor: pointer;
  img {
    position: absolute;
    left: 0;
    top: -5px;
  }
}

@include mobile-only {
  .pool_flex {
    display: block;
  }
  .pool_help {
    display: inline-block;
  }
}
