@import '../../../../assets/css/theme';

.panel-header {
  display: flex;
  align-items: center;
}
.panel-header-item {
  text-align: center;
  margin-right: 50px;
  &:last-child {
    margin-right: 0;
  }
}
.panel-header-item {
  a,
  a:link,
  a:visited,
  .page-item-text {
    display: inline-block;
    font-size: 20px;
    color: $color-text-default-5;
    font-weight: 500;
    padding: 5px 0;
    cursor: pointer;
  }
}
.panel-header-item.panel-header-item-active {
  a,
  .page-item-text {
    color: $color-text-default;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 0;
      width: 30px;
      height: 4px;
      background: linear-gradient(135deg, #9017ff, #5a14e1);
      border-radius: 2px;
      transform: translateX(-50%);
      cursor: pointer;
    }
  }
}
.page-panel-content {
  position: relative;
  padding: 0px 30px 25px;
  background: #2f2f41 url(../../v3/assets/images/v3-bg.svg) right bottom no-repeat;
  background-size: 240px auto;
  border-radius: 0 0 30px 30px;
  min-height: 240px;
}
.mobile-content {
  .panel-header-item {
    a,
    a:link,
    a:visited {
      font-size: 16px;
    }
  }
}

@media screen and (max-width: 750px) {
  .panel-header .panel-header-item {
    a {
      font-size: 16px;
    }
    .page-item-text {
      font-size: 16px;
    }
  }
  .panel-header-item {
    margin-right: 16px;
  }
}
