@import '../../../../assets/css/theme';
@import '../../../../assets/css/var';
@import '../../../../assets/css/mixins';

.pool_apply {
  min-height: 200px;
  border-radius: 10px;
  padding: 30px;
  position: relative;
  @include darkThemeOnly {
    background: url('../../../../assets/images/new-farm/farm_bg.png') no-repeat;
  }
  @include lightThemeOnly {
    background: url('../../../../assets/images/new-farm/farm_bg_light.png') no-repeat;
  }

  > .pool_apply_p {
    @include darkThemeOnly {
      -webkit-text-stroke: 1px $color-text-default-5;
    }
    @include lightThemeOnly {
      -webkit-text-stroke: 1px rgba($color: #722fff, $alpha: 0.7);
    }
    -webkit-text-fill-color: transparent;
    font-family: Helvetica-Bold, Helvetica;
    font-size: 32px;
    font-weight: 600;
    color: transparent;
    position: relative;
    > p {
      @include darkThemeOnly {
        background-image: linear-gradient(to bottom, $white, #c79eff);
      }
      @include lightThemeOnly {
        background-image: linear-gradient(to bottom, #bfa0ff, #5100ff);
      }
      -webkit-text-stroke: transparent;
      -webkit-background-clip: text;
      color: transparent;
      font-family: Helvetica-Bold, Helvetica;
      font-size: 32px;
      font-weight: 600;
      position: absolute;
      top: -2px;
      left: -2px;
      z-index: 1;
    }
  }
  .pool_apply_text {
    margin-top: 20px;
    span {
      font-size: 16px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: $color-text-default;
    }
  }
  .pool_apply_img {
    position: absolute;
    right: 0;
    bottom: 0;
    height: 180px;
  }
}
@include mobile-only {
  .pool_apply {
    .btn_list {
      display: flex;
      flex-direction: column;
      width: 150px;
    }
    .pool_apply_img {
      height: 120px;
    }
  }
}
